import Image from "next/image";

interface Iimage {
  url: string;
  name: string;
}

const ForumCard = ({ image }: { image: Iimage }) => {
  return (
    <div className="">
      <Image
        src={image.url}
        alt={image.name}
        fill={true}
        sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
      ></Image>
    </div>
  );
};

export default ForumCard;
